import {requestD,requestG,requestK,requestP,requestF} from './request.js'

const uploadApi = (data,mode) => requestF('/file/upload' , data, mode) //
const getPublishApi = data => requestG('/publish/list' , data) //
const delPublishApi = data => requestD('/publish/del',data) //
const postPublishApi = data => requestK('/publish/add' , data) //
const collectMeApi = data => requestG('/publish/collectMe', data) //

const updateCheckAPI = data => requestG('/apk' , data) //

const forgetApi = data => requestK('/user/forget', data) //
const registerApi = data => requestK('/user/register', data) //
const loginApi = data => requestK('/user/login', data) //
const logoutApi = data => requestD('/user/logout', data) //
const getUserInfoApi = data => requestG('/user/info', data) //
const updateUserInfoApi = data => requestP('/user/updateInfo',data) //
const initMenuApi = data => requestG('/user/initMenu', data) //


export default {
	uploadApi,
	getPublishApi,
	postPublishApi,
	loginApi,
	logoutApi,
	getUserInfoApi,
	initMenuApi,
	registerApi,
	updateUserInfoApi,
	delPublishApi,
	collectMeApi,
	updateCheckAPI
}